import React from 'react';
import ReactMarkdown from 'react-markdown';

const READMEContent = `
# Company Data Processing and Database Loading App

This application is a full-stack React/Node.js app that facilitates the processing and loading of company data from a CSV file into a SQLite database. It provides various functionalities for data pre-processing, transformation, and database interaction.

## Table of Contents

- [Features](#features)
- [Getting Started](#getting-started)
- [Usage](#usage)
- [API Endpoints](#api-endpoints)
- [Database Schema](#database-schema)
- [Contributing](#contributing)
- [License](#license)

## Features

- CSV file parsing and loading into a DataFrame
- Data pre-processing:
  - Converting company names to title case
  - Converting revenue values to integers
  - Generating new columns based on existing columns (e.g., Domain, Country Codes, GBP Revenues)
- Duplicate removal:
  - Removing duplicate entries based on Name and Website
  - Removing duplicate entries based on Name or Website
- Data validation:
  - Removing rows with missing or invalid data
- Database loading:
  - Sending the processed data to a backend API for loading into a SQLite database
- Database queries:
  - Viewing the loaded company data and financial data through API calls
  - Performing detailed queries on the company and financial data

## Getting Started

To get started with the application, follow these steps:

INSTRUCTIONS PENDING

   The application will be accessible at [http://localhost:3000](http://localhost:3000).

## Usage

1. Upload CSV File:

   - In the CSV page, click on the "Upload CSV" button to select a CSV file containing the company data, and then click Import CSV.
   - The application will parse the CSV file and load it into a DataFrame.

2. Data Pre-processing:

   - By default loading the data will perform various pre-processing tasks, such as converting company names to title case and converting revenue values to integers.
   - Additional functionalities include generating new columns based on existing columns, removing duplicate entries based on specific criteria, and removing rows with missing or invalid data can be activated by clicking the respective buttons.

3. Database Loading:

   - Once all buttons have been pressed, a new button will show. Click on the "Load Data to Database" button to load the processed data into the database.
   - The data will be sent to the backend API, which will insert it into the SQLite database.

4. Viewing Data:

   - Navigate to the "Companies" and "Financials" pages to view the loaded data.
   - The application makes API calls to fetch the company and financial data from the database and displays it in a table format.
   - The table includes the relevant rows and provides a count of the entries/rows in each respective table.

5. Database Queries:

   - Access the "Queries" page to view more details queries on the company and financial data. 
   - The application sends API requests to the backend, which executes the pre-programmed queries on the database and returns the results.

## API Endpoints

The following API endpoints are available:

- GET \`/api/companies\` - Fetch all company data from the database.
- POST \`/api/companies\` - Insert new company data into the database.
- PATCH \`/api/companies/:id\` - Update a company's data by ID.
- DELETE \`/api/companies/:id\` - Delete a company by ID.
- GET \`/api/financials\` - Fetch all financial data from the database.
- POST \`/api/financials\` - Insert new financial data into the database.
- PATCH \`/api/financials/:id\` - Update financial data for a specific company by ID.
- DELETE \`/api/financials/:id\` - Delete financial data for a specific company by ID.

## Database Schema

The application uses a SQLite database with the following schema:

**companies**

| Column       | Type    |
| ------------ | ------- |
| id           | INTEGER |
| name         | TEXT    |
| website      | TEXT    |
| domain       | TEXT    |
| country      | TEXT    |
| country_code | TEXT    |

**financials**

| Column      | Type    |
| ----------- | ------- |
| id          | INTEGER |
| company_id  | INTEGER |
| year        | INTEGER |
| revenue_usd | INTEGER |
| revenue_gbp | INTEGER |

## Contributing

We are currently not taking contributions.

## License

This project is currently unlicensed.
`;

const SQLiteView = () => {
  return (
    <div>
      <ReactMarkdown>{READMEContent}</ReactMarkdown>
    </div>
  );
};

export default SQLiteView;
